// app/store/page.tsx
'use server';

import { createServerComponentClient } from '@supabase/auth-helpers-nextjs'
import { cookies } from 'next/headers'
import { unstable_noStore as noStore } from 'next/cache'

export const dynamic = 'force-dynamic'

export default async function StorePage() {
  noStore();
  
  try {
    const supabase = createServerComponentClient({ cookies })
    
    const { data: products, error } = await supabase
      .from('Habibi')
      .select('*')
      .limit(10); // محدودیت برای جلوگیری از بارگذاری زیاد

    if (error) {
      console.error('Supabase error:', error);
      throw new Error('Failed to fetch products');
    }

    if (!products || products.length === 0) {
      return <div className="p-8 text-center">محصولی یافت نشد</div>;
    }

    return (
      <div className="container mx-auto p-4">
        <h1 className="text-2xl font-bold mb-6">محصولات</h1>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {products.map((product) => (
            <div key={product.id} className="border p-4 rounded-lg">
              <h2 className="font-semibold">{product.title}</h2>
              <p className="text-gray-600">{product.price} تومان</p>
            </div>
          ))}
        </div>
      </div>
    );
  } catch (err) {
    console.error('Page error:', err);
    return (
      <div className="p-8 text-center text-red-500">
        خطا در بارگذاری صفحه فروشگاه
      </div>
    );
  }
}